import React, { Component, useState, useCallback, useEffect } from "react"; // import from react

import { Window, App, Text, Button, View } from "proton-native"; // import the proton-native components


const POMODORO_LEN = 1500; // [s]

const Example = () => {
  // all Components must have a render method

  const initState = {
    count: 0,
    start: false,
    times: 1
  };
  const [count, setCount] = useState(initState.count);
  const [start, setStart] = useState(initState.start);
  const [times, setTimes] = useState(initState.times);

  const reset = () => {
    setCount(initState.count);
    setStart(initState.start);
    setTimes(initState.times);
  };

  useEffect(() => {
      if (!start) {
        return;
      }
      let id = setInterval(() => {
        setCount(count => count + 1);
      }, 1000 / times);
      return () => clearInterval(id);
    },
    [start, times]
  );

  return (
    <App>
      {/* you must always include App around everything */}
      <Window style={{ width: 700, height: 700, backgroundColor: "#fff" }}>
        {/* todos os seus outros componentes v√£o aqui*/}
        <View style={{ margin: 10, flex: 1 }}>
          <View
            style={{
              flexDirection: 'row',
              justifyContent: 'center',
              marginBottom: 10,
            }}
          >
            <Text style={{ fontSize: 55 }}>useCounter</Text>
          </View>
          <View style={{ flexDirection: 'row', marginBottom: 10 }}>
            <Text style={{ fontSize: 50, color: 'darkred' }}>
              {`${count}`}
            </Text>
          </View>
          <View style={{ flexDirection: 'row', marginBottom: 10 }}>
            <Button onPress={() => setStart(true)} title="Start" />
            <Button onPress={() => setStart(false)} title="Pause" />
            <Button onPress={() => reset()} title="Reset" />
          </View>
          <View
            style={{
              flexDirection: 'row',
              justifyContent: 'center',
              marginBottom: 10,
            }}
          >
          </View>
        </View>
      </Window>
    </App>
    );
  }


export default Example;